:root {
  --link-color: #00acdc;
}

body {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  align-items: center;
  height: 100%;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  display: flex;
}

a {
  color: #000;
  align-items: center;
  height: 100%;
  text-decoration: none;
  display: flex;
  position: relative;
}

a:hover, .is-active {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  background: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

